import React from 'react'
import { Link } from 'react-router-dom'
import cardBlock from '../../components/cardBlock/cardBlock';
import requestItem from '../../components/requestItem/requestItem';
import newsItem from '../../components/newsItem/newsItem';

import { useStore } from 'easy-peasy';
import parse from 'html-react-parser'

export default function News() {
  const newsList = useStore(store => store.news.newsList)
  return (
    <div className="container">
      <div className="row">
        <div className="col-lg-8">
          <div>
            {cardBlock(
              <h2> </h2>,
              <div className="list-card no-padding">
                {newsList.length !== 0
                  ? newsList.map((item, index) => <div key={index} className="card">
                    {newsItem(
                      item.id,
                      item.title,
                      item.announce ? <p>{item.announce}</p> : parse(item.content),
                      item.imagePreviewPath,
                      {
                        likesCount: 650,
                        commentsCount: 2
                      },
                      item.updatedAt
                    )}
                  </div>)
                  : <div className="px-2">
                    <p>Loading...</p>
                  </div>
                }
              </div>
            )}
          </div>
        </div>

        <div className="col-lg-4 d-none d-lg-block">
          <div>
            {cardBlock(
              <h2> </h2>,
              <div className="list-card no-padding">
                {Array(3).fill().map((item, index) => <div key={index} className="card">
                  {requestItem(
                    {
                      photo: "https://picsum.photos/50",
                      name: " ",
                      location: ", "
                    },
                    {
                      category: "",
                      location: ""
                    },
                    ",    . ,    ?.."
                  )}
                </div>)}
                <div className="px-2 mt-2">
                  <Link to='/requests'>  <i className="fas fa-angle-double-right"></i></Link>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}
