import React from 'react'
import { Link } from "react-router-dom";
import cardBlock from '../../components/cardBlock/cardBlock';
import comment from '../../components/comment/comment';

import './profile.scss'
import { socialVkontakte, socialFacebook, socialInstagram } from '../../components/socials/socials';
import editIcon from '../../components/editIcon/editIcon';

import { useStore } from 'easy-peasy';
import { ROLE_TYPES } from './constants';

export default function MyProfile() {
  const user = useStore(store => store.profile.user)
  return (
    <div className="container">
      <div className="row">

        <div className="col-lg-4">

          <div className="row no-gutters profile-person">
            <div className="col-6 pr-1 profile-person__photo">
              <img src={user && user.photoFilePath ? user.photoFilePath : "https://picsum.photos/200/250"} alt="" />
            </div>
            <div className="col-6 pl-1">
              <h2 className="profile-person__name">{user && user.fullName ? `${user.firstName} ${user.surName}` : '...'}</h2>
              <Link className="profile-person__button button button_expanded" to="/profile/requests"> </Link>
              <Link className="profile-person__button button button_expanded button_secondary mt-1" to="/requests/create"> </Link>
            </div>
          </div>

          <div className="mt-2">
            {cardBlock(
              <div className="row no-gutters">
                <h2 style={{ marginBottom: 0 }}></h2>
                <div className="ml-auto">
                  {editIcon('/profile/edit')}
                </div>
              </div>,
              <div className="pb-2">
                {user && user.graduationYear
                  ? <table className="profile-info">
                    <tbody>
                      <tr>
                        <td className="profile-info__head">:</td>
                        <td className="profile-info__content">{ROLE_TYPES[user.type]}</td>
                      </tr>
                      <tr>
                        <td className="profile-info__head"> :</td>
                        <td className="profile-info__content">{user.graduationYear}</td>
                      </tr>
                      {user.country &&
                        <tr>
                          <td className="profile-info__head">:</td>
                          <td className="profile-info__content">{}</td>
                        </tr>}
                      {(user.city || user.cityId) &&
                        <tr>
                          <td className="profile-info__head">:</td>
                          <td className="profile-info__content">{user.city || user.cityId}</td>
                        </tr>}
                      {user.competencies &&
                        <tr>
                          <td className="profile-info__head">: </td>
                          <td className="profile-info__content">{user.competencies}</td>
                        </tr>}
                      {user.education &&
                        <tr>
                          <td className="profile-info__head">:</td>
                          <td className="profile-info__content">{user.education}</td>
                        </tr>}
                      {user.workExperiences.length !== 0 &&
                        <tr>
                          <td className="profile-info__head">:</td>
                          <td className="profile-info__content"></td>
                        </tr>}
                      {user.socialNetworks.length !== 0 &&
                        <tr>
                          <td style={{ verticalAlign: 'bottom' }} className="profile-info__head"> .:</td>
                          <td className="profile-info__content">
                            <div className="row no-gutters">
                              <div className="mr-1 mt-1">
                                {socialVkontakte("https://vk.com")}
                              </div>
                              <div className="mr-1 mt-1">
                                {socialFacebook("https://facebook.com")}
                              </div>
                              <div className="mr-1 mt-1">
                                {socialInstagram("https://instagram.com")}
                              </div>
                            </div>
                          </td>
                        </tr>}
                    </tbody>
                  </table>
                  : <p>Loading...</p>}
              </div>
            )}
          </div>

        </div>

        <div className="col-lg-8 mt-2 mt-lg-0">
          {user && user.about &&
            <div className="mb-2">
              {cardBlock(
                <h2> </h2>,
                <div className="pb-2">
                  <p>{user.about}</p>
                </div>
              )}
            </div>}

          <div>
            {cardBlock(
              <h2></h2>,
              <div className="list-card no-padding">
                {user && user.reviews && user.reviews.length
                  ? user.reviews.map((item, key) => <div className="card list-card-item" key={key}>
                    {comment(
                      {
                        photo: "https://picsum.photos/50",
                        name: " ",
                        location: "-, "
                      },
                      " ,    , -   ,       ! , ,    -       ,   .",
                      1,
                      new Date("2019-05-02")
                    )}
                  </div>)
                  : <div className="px-2">
                    <p>    </p>
                  </div> }
              </div>
            )}
          </div>
        </div>

      </div>
    </div>
  )
}
